.about {
  padding: 4em 0;
  text-align: center;
}

.about h2 {
  margin-bottom: 1em;
}

.about p {
  max-width: 800px;
  margin: 0 auto;
  font-size: 1.2em;
  line-height: 1.6;
}

@media (max-width: 768px) {
  .about {
    padding: 2em 0;
  }

  .about p {
    font-size: 1em;
    max-width: 100%;
    padding: 0 1em;
  }
}
